import send from './http'
export default {
  getFleetList (data : any) {
    let req = {
      data: data,
      action: 'ticket.getFleetList'
    }
    return send.fetch(req, 'post')
  },
  getCarList (data : any) {
    let req = {
      data: data,
      action: 'ticket.getCarList'
    }
    return send.fetch(req, 'post')
  },
  getCarInfo (data : any) {
    let req = {
      data: data,
      action: 'ticket.getCarInfo'
    }
    return send.fetch(req, 'post')
  },
  getOrderList (data : any) {
    let req = {
      data: data,
      action: 'ticket.getOrderList'
    }
    return send.fetch(req, 'post')
  },
  orderAllocation (data : any) {
    let req = {
      data: data,
      action: 'ticket.orderAllocation'
    }
    return send.fetch(req, 'post')
  },
  addOrUpdateCar (data : any) {
    let req = {
      data: data,
      action: 'ticket.addOrUpdateCar'
    }
    return send.fetch(req, 'post')
  },
  sendSMS (data : any) {
    let req = {
      data: data,
      action: 'ticket.sendSMS'
    }
    return send.fetch(req, 'post')
  },
  qrValidate (data : any) {
    let req = {
      data: data,
      action: 'qrValidate'
    }
    return send.fetch(req, 'post')
  }
}
